    color: var(--gray-dark);
    -webkit-transition: color 0.5s;
    -moz-transition: color 0.5s;
    -o-transition: color 0.5s;
    letter-spacing: 1px;
}
.nav-menu-link:hover{
    color: var(--pink);
}
.active{
    color: var(--pink);
}
